let retObject = function(callOnDispose){
	this.dispose = function(){
		callOnDispose();
	}	
}
retObject.from = function(...disposables){
	return function(){
		if(disposables.length){
			// disposable
			for(a in args){
				if(typeof args[a].dispose === 'function'){
					args[a].dispose()
				}
			}
			disposables = undefined
		}
	}}
module.exports = retObject