const {	CompletionItemKind } = require("vs/workbench/api/common/extHostTypes");

const Type = {
	/**
	 * 
	 */
	DEFALUT: "DEFALUT",
	/**
	 * 
	 */
	NUMBER: "NUMBER",
	/**
	 * 
	 */
	ATTRIBUTE: "ATTRIBUTE",
	/**
	 * 
	 */
	FUNCTION: "FUNCTION",
	ID: "ID",
	CLASS: "CLASS",
	SELECTOR: "SELECTOR",
	COLOR: "COLOR",
	/**
	 * 
	 */
	COMMENT: "COMMENT",
	/**
	 * 
	 */
	ELEMENT: "ELEMENT",
	/**
	 * 
	 */
	ANCHOR: "ANCHOR",
	/**
	 * 
	 */
	FILE: "FILE",
	IMAGE: "IMAGE",
	/**
	 * 
	 */
	FONT: "FONT",
	/**
	 * 
	 */
	FOLDER: "FOLDER",
	/**
	 * 
	 */
	KEYWORD: "KEYWORD",
	/**
	 * 
	 */
	CODEBLOCK: "CODEBLOCK",
	/**
	 * 
	 */
	OTHER: "OTHER",
	STRING: "STRING",
	ABC:"ABC",
	SNIPPET: "SNIPPET",
	/**
	 * 
	 */
	EVENT: "EVENT"
}

/**
 * 
 * @param {string} kind 
 */
function convert(kind) {
	let type = kind;
	if (typeof kind === 'string') {
		type = CompletionItemKind[kind];
	}
	switch (type) {
		// case :
		//   return Type.NUMBER;   // css  completioItem 
		case CompletionItemKind.Property:
		case CompletionItemKind.Variable:
			return Type.ATTRIBUTE;
		case CompletionItemKind.Method:
		case CompletionItemKind.Function:
		case CompletionItemKind.Constructor:
			return Type.FUNCTION;
			// case :
			//   return Type.ID;
			// case :
			//   return Type.CLASS;
			// case :
			//   return Type.SELECTOR;
		case CompletionItemKind.Color:
			return Type.COLOR;
			// case :
			//   return Type.COMMENT;
			// case :
			//   return Type.ELEMENT;  // html
			// case :
			//   return Type.ANCHOR;   // @obsolete
		case CompletionItemKind.File:
			return Type.FILE;
			// case :
			//   return Type.IMAGE;  // <img src=""  
			// case :
			//   return Type.FONT;  // font    Type.Attribute 
		case CompletionItemKind.Folder:
			return Type.FOLDER;
		case CompletionItemKind.Keyword:
		case CompletionItemKind.Operator:
			return Type.KEYWORD;
			// case :
			//   return Type.CODEBLOCK; // @obsolete
			// case :
			//   return Type.OTHER; // @obsolete
		case CompletionItemKind.Text:
			return Type.ABC;
		case CompletionItemKind.Snippet:
			return Type.SNIPPET;
		case CompletionItemKind.Event:
			return Type.EVENT;
		default:
			return Type.DEFALUT;
	}
}


module.exports = convert;
